import { Component, OnInit, OnDestroy } from '@angular/core';
import { DomSanitizer } from '@angular/platform-browser';
import { Router } from '@angular/router';
import { TradeService } from '../../../common/trade/trade.service';
import { UserService } from '../../../common/user';
import "rxjs/add/operator/takeWhile";
import { Observable } from 'rxjs/Observable';
import { HttpClient } from '@angular/common/http';

declare var BoxesFx: any;

@Component({
  selector: 'app-main-dispensaries',
  templateUrl: './main-dispensaries.component.html',
  styleUrls: ['./main-dispensaries.component.css']
})
export class MainDispensariesComponent implements OnInit, OnDestroy {

  private alive = true;
  private dispensaries;
  private recomendations;
  private delivery;
  private nonDelivery;
  private currentPage = 1;
  private previousPageSearch = 0;
  private pageElements = 9;
  private sortType = 1;
  private value;
  private pick;
  bannerDispensaries = 'dispensaries-banner.jpg';
  flowerimg = 'flower.png';
  searchimg = 'search.png';
  totalSearch: number;
  styles= [
    {
      "elementType": "geometry",
      "stylers": [
        {
          "color": "#f5f5f5"
        }
      ]
    },
    {
      "elementType": "labels.icon",
      "stylers": [
        {
          "visibility": "off"
        }
      ]
    },
    {
      "elementType": "labels.text.fill",
      "stylers": [
        {
          "color": "#616161"
        }
      ]
    },
    {
      "elementType": "labels.text.stroke",
      "stylers": [
        {
          "color": "#f5f5f5"
        }
      ]
    },
    {
      "featureType": "administrative.land_parcel",
      "elementType": "labels.text.fill",
      "stylers": [
        {
          "color": "#bdbdbd"
        }
      ]
    },
    {
      "featureType": "poi",
      "elementType": "geometry",
      "stylers": [
        {
          "color": "#eeeeee"
        }
      ]
    },
    {
      "featureType": "poi",
      "elementType": "labels.text.fill",
      "stylers": [
        {
          "color": "#757575"
        }
      ]
    },
    {
      "featureType": "poi.park",
      "elementType": "geometry",
      "stylers": [
        {
          "color": "#e5e5e5"
        }
      ]
    },
    {
      "featureType": "poi.park",
      "elementType": "labels.text.fill",
      "stylers": [
        {
          "color": "#9e9e9e"
        }
      ]
    },
    {
      "featureType": "road",
      "elementType": "geometry",
      "stylers": [
        {
          "color": "#ffffff"
        }
      ]
    },
    {
      "featureType": "road.arterial",
      "elementType": "labels.text.fill",
      "stylers": [
        {
          "color": "#757575"
        }
      ]
    },
    {
      "featureType": "road.highway",
      "elementType": "geometry",
      "stylers": [
        {
          "color": "#dadada"
        }
      ]
    },
    {
      "featureType": "road.highway",
      "elementType": "labels.text.fill",
      "stylers": [
        {
          "color": "#616161"
        }
      ]
    },
    {
      "featureType": "road.local",
      "elementType": "labels.text.fill",
      "stylers": [
        {
          "color": "#9e9e9e"
        }
      ]
    },
    {
      "featureType": "transit.line",
      "elementType": "geometry",
      "stylers": [
        {
          "color": "#e5e5e5"
        }
      ]
    },
    {
      "featureType": "transit.station",
      "elementType": "geometry",
      "stylers": [
        {
          "color": "#eeeeee"
        }
      ]
    },
    {
      "featureType": "water",
      "elementType": "geometry",
      "stylers": [
        {
          "color": "#c9c9c9"
        }
      ]
    },
    {
      "featureType": "water",
      "elementType": "labels.text.fill",
      "stylers": [
        {
          "color": "#9e9e9e"
        }
      ]
    }
  ];

  markers: any[] = []
  images: Array<any>;


  constructor(private router: Router,
    private tradeService: TradeService,
    private userService: UserService,
    private sanitizer: DomSanitizer, private http: HttpClient) {
    this.images = [

      {
        "sType": "div", "styles": { "width": "100%", height: "780px" }, "content": `
        <div style="background-image: url('/assets/images/deals-banner.jpg'); width:100%; height:780px;">
      <div  class="slide-middle-txt animated">
      <h1><span >Welcome</span> <span class="color-txt">to</span> Panda Weed</h1>
  </div>
  </div>
      ` }];
  }

  ngOnInit() {
    if (window.screen.width <= 959) {
      this.bannerDispensaries = 'dispensaries-banner-xs.jpg';
      this.flowerimg = 'flower-xs.png';
      this.searchimg = 'search-xs.png';
    }
    this.searchDispiensaries();
    this.searchRecomendations();
  }
  pageChangeSearch(a, b, c) {
    console.log("preview page", this.previousPageSearch, this.currentPage);
    this.searchDispiensaries();
    this.previousPageSearch = this.currentPage;
  }
  searchDispiensaries() {
    this.dispensaries = [];
    this.tradeService.getTrades("4", "4", this.currentPage - 1, this.pageElements, this.sortType).takeWhile(() => this.alive).subscribe((res) => {
      this.dispensaries = res;
      if (this.dispensaries && this.dispensaries[0]) {
        this.totalSearch = this.dispensaries[0].totalCount;
      }
      this.reloadMarkers();
    })
  }
  searchRecomendations() {
    this.tradeService.getTrades("4", "4", this.currentPage, this.pageElements, this.sortType).takeWhile(() => this.alive).subscribe((res) => {
      if (res) {
        this.recomendations = res.slice(0, 3);
      }

    })
  }
  searchWithDelivery() {
    this.tradeService.getTrades("4", "4", this.currentPage, this.pageElements, this.sortType).takeWhile(() => this.alive).subscribe((res) => {
      this.delivery = res;
    })
  }
  searchNonDelivery() {
    this.tradeService.getTrades("4", "4", this.currentPage, this.pageElements, this.sortType).takeWhile(() => this.alive).subscribe((res) => {
      this.nonDelivery = res;
    })
  }

  reloadMarkers() {
    this.markers = [];
    for (let dispensarie of this.dispensaries) {
      let imageURL = "";
      if (dispensarie.media && Array.isArray(dispensarie.media) && dispensarie.media.length > 0) {
        imageURL = dispensarie.media[0].mediaUrl;
      }
      this.markers.push({ lat: dispensarie.latitude, lng: dispensarie.longitude, visible: false, draggable: false, label: dispensarie.title, image: imageURL });
    }
  }

  gotoDispensarie(dispensarie: any) {
    this.router.navigate(['/dispensaries/view-dispensaries', dispensarie]);
  }

  splitText(text) {
    if (!text) {
      return text;
    }
    //TODO constant
    if (text.length > 150) {
      return text.substring(0, 150) + "..."
    }
    return text;
  }

  ngOnDestroy() {
    this.alive = false;
  }

  // google maps zoom level
  zoom: number = 11;

  lat: number = 43.661034;
  lng: number = -79.42991;

  clickedMarker(label: string, index: number) {
    console.log(`clicked the marker: ${label || index}`)
    for (const markeI in this.markers) {
      let marke = this.markers[markeI];
      marke.visible = false;
      if (parseInt(markeI) === index) {
        console.log(parseInt(markeI), "===", index);
        marke.visible = true;
      }
      console.log("MARKER", marke);
    }
  }

  mapClicked($event) {
    this.markers.push({
      lat: $event.coords.lat,
      lng: $event.coords.lng
    });
  }

  lookupAsync = (query: string): Observable<any[]> => {
    if (!query) {
      return null;
    }
    return this.userService.search(query).map(response => response.trades);
  }
  changed() {
    this.gotoDispensarie(this.pick.id);
  }

  markerDragEnd(m: marker, $event: MouseEvent) {
    console.log('dragEnd', m, $event);
  }



}

interface marker {
  lat: number;
  lng: number;
  label?: string;
  draggable?: boolean;
  image?: string;
}



