import { NgModule } from '@angular/core';

import { CommonModule } from '@angular/common';
import { AgmCoreModule } from '@agm/core';
import { NglModule } from 'ng-lightning/ng-lightning';
import { TradeCardModule } from '../../common/components/trade';
import { CardModule } from '../../common/components/card';
import { FlexLayoutModule } from '@angular/flex-layout';
import { SliderModule } from '../slider';

import { DispensariesRoutingModule } from './dispensaries.routing';
import { MainDispensariesComponent } from './main-dispensaries/main-dispensaries.component';
import { StoreDispensariesComponent } from './store-dispensaries/store-dispensaries.component';
import { VoucherListDispensariesComponent } from './store-dispensaries/voucher-list-dispensaries/voucher-list-dispensaries.component';
import { VoucherItemDispensariesComponent } from './store-dispensaries/voucher-item-dispensaries/voucher-item-dispensaries.component';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';



@NgModule({
  imports: [
    CommonModule,
    DispensariesRoutingModule,
    AgmCoreModule,
    NglModule,
    SliderModule,
    FormsModule,
    ReactiveFormsModule,
    FlexLayoutModule,
    TradeCardModule,
    CardModule
  ],
  declarations: [
    MainDispensariesComponent,
    StoreDispensariesComponent,
    VoucherListDispensariesComponent,
    VoucherItemDispensariesComponent
  ]
})
export class DispensariesModule { }



