import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { MainDispensariesComponent } from './main-dispensaries/main-dispensaries.component';
import { StoreDispensariesComponent } from './store-dispensaries/store-dispensaries.component';
import { VoucherListDispensariesComponent } from './store-dispensaries/voucher-list-dispensaries/voucher-list-dispensaries.component';
import { VoucherItemDispensariesComponent } from './store-dispensaries/voucher-item-dispensaries/voucher-item-dispensaries.component';

const DispensariesRoutes: Routes = [
  { path: '', component: MainDispensariesComponent },
  { path: 'view-dispensaries', loadChildren: './view-dispensaries/view-dispensaries.module#ViewDispensariesModule' },
  { path: 'admin-dispensaries', loadChildren: './admin-dispensaries/admin-dispensaries.module#AdminDispensariesModule' }
];

@NgModule({
  imports: [RouterModule.forChild(DispensariesRoutes)],
  exports: [RouterModule]
})
export class DispensariesRoutingModule { }



